"use client";

import React from "react";
import UserProgress from "../../progress/components/UserProgress";
import ModuleCard from "../../progress/components/ModuleCard";

// Datos de ejemplo para los niveles
const levels = [
  {
    id: 1,
    label: "Principiante",
    completedIcon: "/assets/levels/level-1-completed.png", // Imagen completada
    state: "completed", // Nivel completado
    subtitle: "Nivel A1",
    modules: [
      { id: "module-1", title: "Getting Started: The English Alphabet", status: "completed" },
      { id: "module-2", title: "Greetings and Basic Politeness", status: "in-progress" },
      { id: "module-3", title: "Introducing Yourself and Others", status: "locked" },
    ],
  },
  {
    id: 2,
    label: "Intermedio",
    completedIcon: "/assets/levels/level-2-completed.png", // Imagen completada
    lockedIcon: "/assets/levels/level-2-locked.png", // Imagen bloqueada
    state: "in-progress", // Nivel en progreso
    subtitle: "Nivel A2",
    modules: [
      { id: "module-4", title: "Building Longer Sentences", status: "locked" },
      { id: "module-5", title: "Describing Daily Routines", status: "locked" },
    ],
  },
  {
    id: 3,
    label: "Avanzado",
    completedIcon: "/assets/levels/level-3-completed.png", // Imagen completada
    lockedIcon: "/assets/levels/level-3-locked.png", // Imagen bloqueada
    state: "locked", // Nivel bloqueado
    subtitle: "Nivel B1",
    modules: [
      { id: "module-6", title: "Mastering Past Tenses", status: "locked" },
      { id: "module-7", title: "Talking About Future Plans", status: "locked" },
    ],
  },
];

const ProgressPage: React.FC = () => {
  return (
    <div className="p-6">
      {/* Barra de progreso y encabezado */}
      <UserProgress username="Camila Gómez" levels={levels} />

      {/* Lista de niveles y módulos */}
      <h1 className="text-2xl font-bold text-primary-900 mb-6">Mi Progreso</h1>
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {levels.map((level) => (
          <div key={level.id} className="flex flex-col gap-4">
            <h2 className="text-lg font-bold text-primary P16">{level.label}</h2>
            <p className="text-sm text-neutral-600">{level.subtitle}</p>
            <div className="flex flex-col gap-2">
              {level.modules.map((module) => (
                <ModuleCard
                  key={module.id}
                  title={module.title}
                  status={module.status}
                  href={
                    module.status !== "locked"
                      ? `/dashboard/progress/module/${module.id}`
                      : undefined
                  }
                />
              ))}
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default ProgressPage;
